	module.exports = {"data":{"site":{"siteMetadata":{"title":"Fitness Map","author":"Airsyn"}},"markdownRemark":{"id":"/Users/airsyn/Downloads/gatsby-starter-blog-master/src/pages/hello-world/index.md absPath of file >>> MarkdownRemark","html":"<h3>Описание:</h3>\n<p>Это упражнение направлено на ту же группу мышц, что и жим штанги лежа. Но отличается от указанного упражнения тем, что жим гантелей лежа выполняется с большей амплитудой, что способствует большему растягиванию и сокращению грудных мышц, задействует большее число мышц-стабилизаторов.</p>\n<p>Если вы хотите правильно развивать грудные мышцы и увеличивать силовые показатели, то вы должны регулярно выполнять жимы гантелями.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>Возьмите гантели в каждую руку, сядьте с ними на горизонтальную силовую скамью. Положите гантели на бедра и после этого лягте на скамью.</li>\n  <li>Исходное положение: спина прогнута в пояснице, стопы ног упираются в пол. Руки с гантелями находятся на уровне плечей, грифы гантелей расположены на одной линии.</li>\n  <li>На выдохе – вернитесь в исходное положение, максимально растягивая грудь.</li>\n  <li>Проделайте необходимое количество повторений.</li>\n  <li>Повторите упражнение нужное число раз.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Во время выполнения упражнения следите за траекторией – гантели должны двигаться только в одной плоскости.</li>\n  <li>Если в верхней точке разворачивать руки ладонями друг к другу и сильнее сводить гантели, то ваши грудные мышцы сократятся сильнее.</li>\n  <li>Нижняя точка жима гантелей - когда гантели коснутся плеч. Не ниже.</li>\n  <li>Полностью выпрямляя локти, вы включаете в работу трицепсы рук. В зависимости от необходимого результата выбирайте технику выполнения.</li>\n  <li>Чтобы не нанести себе травму, по окончании упражнения вставайте следующим образом: немного приподнимите ноги, гантели положите на бедра и после этого вставайте, перенося вес гантелей на ноги.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/4yzmijgmDc8\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"title":"Жим гантелей лежа","date":"July 12, 2017"}}},"pathContext":{"slug":"/hello-world/","previous":{"fields":{"slug":"/hi-folks/"},"frontmatter":{"title":"New Beginnings","path":null,"image":null,"image_2":null,"image_3":null,"image_4":null,"filterby":null,"efficiency":null,"injury":null,"bodypart":null,"filtertype":null,"type":null,"filterinventory":null,"inventory":null,"filterplace":null}},"next":null}}

